body {
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.modal {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  max-width: 80%;
  text-align: center;
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
}

.close {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

